import { useContext } from "react";
import { contactContext } from "../../Context/contactContext";
import { Link } from "react-router-dom";
import { Spinner } from "../";
import { COMMENT, GREEN, PURPLE } from "../../helpers/colors";
import { Form, Formik,Field,ErrorMessage } from "formik";
import { contactSchema } from "../../validations/contactvalidations";

const AddContact = () => {
    const { loading, groups, createContact,/*errors*/ } = useContext(contactContext);

    return (
        <>
            {loading ? (<Spinner />) : (
                <>
                    <section className="p-3">
                        <img src={require("../../assets/man-taking-note.png")}
                            height="400px"
                            style={{
                                position: "absolute", zIndex: "-1", top: "130px", left: "100px", opacity: "50%"
                            }}
                        />
                        <div className="container">
                            <div className="row">
                                <div className="col">
                                    <p className="h4 fw-bold text-center" style={{ color: GREEN }} >
                                          
                                    </p>
                                </div>
                            </div>
                            <hr style={{ backgroundColor: GREEN }} />
                            <div className="row mt-5">
                                <div className="col-md-4">
                                    {/*{
                                        errors?.map((error,index)=>(
                                            <p key={index} className='bg-danger'>{error.message}</p>
                                        ))
                                    }*/}
                                    <Formik
                                        initialValues={{//             
                                            fullname: '',
                                            photo: '',
                                            mobile: '',
                                            email: '',
                                            job: '',
                                            group: ''
                                        }}
                                        validationSchema={contactSchema}
                                        onSubmit={(values) => {//         values  
                                            createContact(values);//  createcontact values 
                                        }}>
                                                <Form >
                                                    <div className="mb-2">
                                                        <Field name="fullname"
                                                         /*value={formik.values.fullname} onChange={formik.handleChange} onBlur={formik.handleBlur}*/  type="text" className="form-control" placeholder="   " />
                                                       <ErrorMessage name="fullname" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>
                                                    </div>
                                                    <div className="mb-2">
                                                        <Field name="photo" type="text" className="form-control" placeholder=" " />
                                                       <ErrorMessage name="photo" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>
                                                    </div>

                                                    <div className="mb-2">
                                                        <Field name='mobile' type="number" className="form-control" placeholder=" " />
                                                        <ErrorMessage name="mobile" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>

                                                    </div>
                                                    <div className="mb-2">
                                                        <Field name="email" type="email" className="form-control" placeholder="" />
                                                       <ErrorMessage name="email" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>
                                                    </div>
                                                    <div className="mb-2">
                                                        <Field name="job"  type="text" className="form-control" placeholder="" />
                                                       <ErrorMessage name="job" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>
                                                    </div>

                                                    <div className="mb-2">
                                                        <Field as='select' name="group"
                                                             className="form-control" >
                                                            <option value=""> </option>
                                                            {
                                                                groups.length > 0 && groups.map((group) => (
                                                                    <option key={group.id} value={group.id} >
                                                                        {group.name}
                                                                    </option>
                                                                ))
                                                            }

                                                        </Field>
                                                       <ErrorMessage name="group" render={(msg)=>(<div className="text-danger">{msg}</div>)}/>
                                                    </div>
                                                    <div className="mx-2">
                                                        <input  type="submit" className="btn mx-2" style={{ backgroundColor: PURPLE }} value=" " />
                                                        <Link to={"/Contacts"} className="btn" style={{ backgroundColor: COMMENT }} value=""></Link>
                                                    </div>




                                                </Form>
                                      

                                    </Formik>

                                </div>
                            </div>
                        </div>

                    </section>
                </>
            )}
        </>
    )
}
export default AddContact;